.profile-container {
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 10%;
  min-height: fit-content;
  background-color: #1a374d;
  background-size: cover;
  padding-bottom: 10rem;
  clip-path: polygon(
    50% 0%,
    100% 0,
    100% 35%,
    100% 100%,
    80% 90%,
    50% 100%,
    20% 90%,
    0 100%,
    0 29%,
    0 0
  );
}
.pro-parent {
  display: flex;
  align-items: center;
}

.pro-details {
  padding: 0 2rem;
  text-align: center;
}

.social-icons a {
  font-size: 1.5rem;
  color: #fff;
  padding: 0 1rem;
  transition: all 0.3s ease-in-out;
}
.social-icons a:hover {
  color: #f76e11;
}
.name {
  margin-top: 1rem;
  margin-bottom: 0;
}
.name span {
  font-size: 1.8rem;
  font-weight: 500;
  color: #fff;
}
.name .highlght-text {
  color: #f76e11;
  font-size: 2rem;
}
.primary-text h1 {
  font-weight: 400;
  font-size: 1.9rem;
  margin: 10px 0;
  color: #fff;
}
.role {
  font-size: 1.3rem;
  font-weight: 400;
  color: #fff;
  margin-bottom: 2rem;
}
.profile-btn {
  margin-top: 1rem;
}
.cta-btn {
  border: none;
  padding: 0.2rem 2rem;
  margin: 5px 10px;
  border-radius: 20px;
  font-size: 1rem;
  background: none;
}

.hire {
  border: 2px solid #fff;
  color: #f76e11;
  transition: all 0.3s ease;
}
.hire:hover {
  border: 2px solid #f76e11;
  color: #fff;
}
.resume {
  border: 2px solid #f76e11;
  background-color: #f76e11;
  color: aliceblue;
  transition: all 0.3s ease;
}
.resume:hover {
  border: 2px solid #f76e11;
  background: none;
  color: #fff;
}

.profile-pic {
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 1px 0px white;
  border-radius: 50%;
  height: 320px;
  width: 320px;
  margin: 0 0 0 100px;
}
.pro-pic-bg {
  height: 100%;
  width: 100%;
  background-image: url(../../assests/Home/home.jpeg);
  background-size: contain;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center;
  transition: all 0.3s ease-in-out;
}
.pro-pic-bg:hover {
  transform: scale(1.1);
}
.ham-icon-menu {
  position: absolute;
  z-index: 999;
  width: 100%;
  background-color: #1a374d;
  height: 30vh;
  top: 60px;
  right: 1500px;
  padding: 30px 0;
  transition: 0.3s ease;
}

/* Ham menu */

@media only screen and (max-width: 560px) {
  .profile-container {
    padding-bottom: 2rem;
    clip-path: none;
    padding-top: 1rem;
    max-width: 100%;
  }
  .pro-parent {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .profile-pic {
    border-radius: 50%;
    height: 280px;
    width: 280px;
    margin: 10px 0;
  }
  .role {
    font-size: 1rem;
  }
  .br {
    display: none;
  }
  .cta-btn {
    padding: 0.4rem 2rem;
    margin: 5px 10px;
    font-size: 1.2rem;
  }

  .ham-menu-btn div {
    background-color: #fff;
    height: 2px;
    width: 34px;
    margin: 10px;
    transition: all 0.3s ease-in;
  }
  .social-icons {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .ham-menu-btn.open .linemiddle {
    transform: translateX(-50px);
    background: transparent;
  }

  .ham-menu-btn.open .linetop {
    transform: rotate(45deg) translateY(17px);
  }
  .ham-menu-btn.open .linebottom {
    transform: rotate(-45deg) translateY(-17px);
  }

  .ham-icon-menu {
    position: absolute;
    width: 100%;
    background-color: #1a374d;
    height: 100vh;
    top: 88px;
    right: 500px;
    padding: 30px 0;
    transition: 0.7s ease;
    opacity: 0.6;
  }

  .ham-icon-menu li {
    list-style: none;
    margin: 30px;
  }
  .nav-link-item .atag {
    text-decoration: none;
    color: #fff;
    font-size: 26px;
  }
  .ham-icon-menu.open {
    right: 0;
    transition: 0.7s ease;
    opacity: 1;
  }
  .name span {
    font-size: 25px;
  }
  .name .highlght-text {
    font-size: 25px;
  }
  .primary-text h1 {
    font-size: 26px;
  }
}

@media only screen and (max-width: 440px) {
  .ham-menu-btn {
    margin-left: auto;
  }
  .ham-menu-btn div {
    background-color: #fff;
    height: 2px;
    width: 26px;
    margin: 6px;
    transition: all 0.3s ease-in;
  }
  .ham-menu-btn.open .linetop {
    transform: rotate(45deg) translateY(11.5px);
  }
  .ham-menu-btn.open .linebottom {
    transform: rotate(-45deg) translateY(-11.5px);
  }
  .social-icons a {
    font-size: 1.5rem;
    padding: 0.8rem;
  }
  .social-icons {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
  .ham-icon-menu {
    height: 100vh;
  }
}

@media only screen and (max-width: 350px) {
  .ham-menu-btn {
    margin-left: auto;
  }
  .ham-menu-btn div {
    width: 22px;
  }

  .social-icons a {
    font-size: 1.3rem;
    padding: 0.6rem;
  }
  .social-icons {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
  .ham-icon-menu {
    height: 100vh;
  }
  .name span {
    font-size: 20px;
  }
  .name .highlght-text {
    font-size: 20px;
  }
  .primary-text h1 {
    font-size: 20px;
  }
  .role {
    font-size: 12px;
  }
  .cta-btn {
    font-size: 14px;
    padding: 0.2rem 1.3rem;
    margin: 5px 10px;
  }
  .profile-pic {
    border-radius: 50%;
    height: 240px;
    width: 240px;
    margin: 10px 0;
  }
  .nav-link-item .atag {
    font-size: 20px;
  }
}
