.contact {
  max-width: 1100px;
  height: 100%;
  background-color: #1a374d;
  border-radius: 10px;
  margin: 10px auto;
  box-shadow: 0 0 20px -2px #1f2235;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-bottom: 10px;
}
.contact-title {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 30px;
}
.contact-heading {
  padding: 15px;
  flex-basis: 0;
  flex-grow: 1;
  min-width: 0;
  max-width: 100%;
}
.con-tit {
  color: #fff;
  font-size: 23px;
  font-weight: 700;
  letter-spacing: 4px;
}
.social-contact a {
  color: #fff;
  font-size: 25px;
  font-weight: 500;
  margin-left: 20px;
  transition: 0.3s ease;
}
.social-contact a:hover {
  color: #f76e11;
  font-size: 30px;
}

.contactform {
  display: flex;
}
.imgform {
  margin: 0 10px;
}
.imgform h3{
  color: black;
  margin-bottom: 10px;
}
.con-img {
  background-image: url(../../assests/Contact/cont.jpeg);
  width: 100%;
  height: 150px;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0.5;
}
.contactform form {
  width: auto;
  display: flex;
  flex: 1;
  margin: 0 20px;
  flex-direction: column;
  justify-content: center;
  background-color: #fff;
  box-shadow: 0 0 20px -2px #1f2235;
  border-radius: 12px;
  color: #1f2235;
  padding: 15px;
  margin-top: -50px;
  opacity: 0.9;
}

form label {
  margin-bottom: 5px;
  letter-spacing: 5px;
  color: black;
}

form input,
form textarea {
  border: none;
  background-color: rgb(228, 224, 224);
  outline: none;
  border-radius: 5px;
  resize: none;
  padding: 0.2rem 0.8rem;
  margin-bottom: 20px;
  transition: 0.3s ease;
}

form button {
  border: 2px solid #1a374d;
  width: 130px;
  padding: 12px 20px;
  border-radius: 10px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  color: #fff;
  background-color: #1a374d;
}

form button:hover {
  border: 2px solid #f76e11;
  background-color: #fff;
  color: #1a374d;
  transition: 0.3s ease-in;
}

.greeting{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.greeting P{
  margin: 0;
  font-size: 1rem;
  text-transform: capitalize;
  font-weight: 500;
  color: black;
}
.greeting span{
  color: #f76e11;
  text-align: center;
}

@media only screen and (max-width: 560px) {
  .contact {
    max-width: 100%;
    margin: 0 5px 20px 5px;
    flex-direction: column;
  }
  .contact-heading {
    text-align: center;
  }
  .imgform h3{
      text-align: center;
  }
  .contactform {
    flex-direction: column;
  }
  .contactform form {
    margin-top: 5px;
  }
}

