.scroll-top {
  position: relative;
  cursor: pointer;
}

.top {
  background-color: #f76e11;
  width: 50px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 30px;
  bottom: 35px;
  border-radius: 10px;
}
.top i {
  font-size: 30px;
  color: #fff;
  animation-name: bounce;
  animation-duration: 1s;
  animation-delay: 1s;
  animation-iteration-count: infinite;
}

@keyframes bounce {
  0%{
transform: translateY(-6px);
  }
  50%{
    transform: translateY(0);
  }
  100%{
    transform: translateY(-6px);
  }
}
