.project {
  padding: 50px 100px;
  display: flex;
  flex-direction: column;
  align-content: center;
  text-align: center;
}

.project-list {
  padding-top: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}

@media only screen and (max-width: 560px) {
  .project{
    padding: 10px 20px;
  }
  
  .project-list{
        padding-top: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center; 
    }
}

