.topcta {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 5px;
  position: sticky;
  z-index: 999;
  top: 0;
  background-color: #1a374d;
}
.topcta a {
  text-decoration: none;
  color: #fff;
  font-size: 1.1rem;
  font-weight: 500;
}

@media only screen and (max-width: 350px){
    .topcta a {
        font-size: 0.8rem;
        font-weight: 400;
    }
}
