.about {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.about-heading h1 {
  margin: 0;
  font-size: 2rem;
  padding: 30px 0 2px 0;
}
.sub-heading {
  margin: 0;
}
.sub-heading p {
  margin: 0;
  padding: 0 0 5px 0;
  font-size: 15px;
}
.sapreting-line {
  display: flex;
  align-items: center;
  position: relative;
  width: 180px;
  margin: 10px 0 0;
}
.sap-line {
  width: 100%;
  height: 2px;
  background-color: #1f221f;
}
.sap-bub {
  height: 10px;
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
}
.sap-bub div {
  width: 40px;
  border-radius: 10px;
  background-color: #f76e11;
}
.about-card {
  display: flex;
  margin: 2rem 0;
  box-shadow: 0 0 20px -2px #1f2235;
  border-radius: 10px;
}

.side-photo {
  background-image: url(../../assests/about/thebose.jpg);
  width: 450px;
  height: 600px;
  background-size: cover;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.about-detail {
  width: 450px;
  height: 580px;
  padding: 5px 40px;
}

.highlight {
  margin-top: 6rem;
}

.higlight-list {
  display: flex;
  align-items: center;
  margin: 5px 0;
  font-size: 12px;
}
.higlight-list p {
  margin: 0;
  font-size: 1rem;
}
.higlight-list-blob {
  height: 12px;
  width: 12px;
  margin: 0 10px 0 0;
  background-color: #ff5823;
  border-radius: 50%;
}

.about-cta-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 4rem;
}
.About-hire {
  border: 2px solid black;
  color: black;
  font-size: 1rem;
  font-weight: 600;
  background: none;
  padding: 0.7rem 2rem;
  margin-right: 1rem;
  border-radius: 20px;
  cursor: pointer;
}
.About-hire:hover {
  border: 2px solid #ff5823;
  color: #ff5823;
  transition: 0.2s ease-in-out;
}

.About-resume {
  border: 2px solid #ff5823;
  background-color: #ff5823;
  font-size: 1rem;
  font-weight: 600;
  padding: 0.7rem 2rem;
  color: white;
  border-radius: 20px;
  cursor: pointer;
}

.About-resume:hover {
  color: #1f221f;
  transition: 0.2s ease-in-out;
}

@media only screen and (max-width: 560px) {
  .side-photo {
    display: none;
  }
  .about-card {
    margin: 35px 10px;
    padding-bottom: 10px;
  }
  .about-detail {
    width: 100%;
    height: 100%;
  }
  .about-cta-btn {
    flex-direction: column;
    margin-top: 2rem;
  }
  .about-cta-btn button {
    margin: 10px 0;
    width: 250px;
  }

  .my-word p {
    font-size: 15px;
    margin-top: 20px;
  }
  .highlight {
    margin-top: 2rem;
  }
  .main-p{
    font-size: 1rem;
    font-weight: 500;
  }
}

